.charTurnComplete {
  grid-column: turn-complete;
  margin-right: 10px;
}

.charInfo,
.summonInfo {
  grid-column: info;
}

.charInfo .charInfoContainer,
.summonInfo .charInfoContainer {
  display: table;
  font-size: x-large;
}

.charInfo .charInfoContainer.summonInfoContainer,
.summonInfo .charInfoContainer.summonInfoContainer {
  font-size: medium;
}

.charInfo .charInfoContainer .charName,
.summonInfo .charInfoContainer .charName {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
}

.charInfo .charInfoContainer .charIcon,
.summonInfo .charInfoContainer .charIcon {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
  padding-right: 6px;
  width: 18px;
  height: 18px;
}

.charInfo .charInfoContainer .charIcon img,
.summonInfo .charInfoContainer .charIcon img {
  width: 18px;
  height: 18px;
}

.charSeparator {
  grid-column: 1/-1;
  height: 8px;
}

.charInit {
  grid-column: initiative;
  font-size: x-large;
  margin-left: 20px;
}

.charMoveButtons {
  grid-column: move-buttons;
  margin-left: 4px;
}

.charMoveButtons .charMoveButtonContainer {
  position: relative;
  width: 18px;
}

.charMoveButtons .charMoveButtonContainer .moveUp,
.charMoveButtons .charMoveButtonContainer .moveDown {
  position: absolute;
  width: 0px;
  height: 0px;
  border-left: 9px solid transparent;
  border-right: 9px solid transparent;
  display: inline-block;
}

.charMoveButtons .charMoveButtonContainer .moveUp {
  top: -11px;
  border-bottom: 9px solid black;
}

.charMoveButtons .charMoveButtonContainer .moveDown {
  top: 2px;
  border-top: 9px solid black;
}

.charDelete {
  grid-column: delete;
  margin-left: 16px;
}
